:root {
  --primary: #337ab7;
  --secondary: #6c757d;
  --warning: #f0ad4e;
  --danger: #d9534f;
  --success: #5cb85c;
  --white: #ffffff;
  --dark: #181717;
}

footer {
  padding: 2.6rem 0rem;
  width: 100%;
  text-align: center;
  position: relative;
}

.FooterText {
  font-family: Corbel;
  font-weight: 400;
  font-size: 16px;
  width: 10%;
  position: absolute;
  left: 50%;
  transform: translate(-80%, -20%);
}

.footer--primary {
  background-color: var(--primary);
  color: var(--white);
  border: none;
}

.footer--secondary {
  background-color: var(--secondary);
  color: var(--white);
  border: none;
}

.footer--warning {
  background-color: var(--warning);
  color: var(--white);
  border: none;
}

.footer--danger {
  background-color: var(--danger);
  color: var(--white);
  border: none;
}

.footer--success {
  background-color: var(--success);
  color: var(--white);
  border: none;
}

.footer--white {
  background-color: #f4f4f4;
  color: var(--dark);
  border: 1px solid #a2a2a2;
}

.footer--dark {
  background-color: var(--dark);
  color: var(--white);
  border: none;
}

.footer--fixed {
  position: fixed;
  left: 0;
  bottom: 0;
}