.input-container {
    position: relative;
    height: 56px;
    min-width: 280px;
    color: #616161;
    margin-top: 30px;
}

.input-container input {
    width: 100%;
    height: 100%;
    border: none;
    padding-left: 12px;
    outline: none;
    box-sizing: border-box;
}

.input-container input:hover {
    background: #f5f5f5;
}

.input-container input:disabled:hover {
    background: rgb(235, 235, 228);
}


.input-container label {
    position: absolute;
    left: 0;
    bottom: 0;
    color: rgba(0, 0, 0, .6);
    pointer-events: none;
    border-bottom: 1px solid #616161;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .1);
    border-radius: 4px 4px 0 0;
}

.input-container input:disabled+label {
    color: #8a8a8a;
    border-bottom: none;
}


.input-container input:focus+label {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    bottom: -1px;
    border-bottom: 2px solid #6200ee;
    transition: border 0.3s ease-in-out;
    background-color: rgba(0, 0, 0, .22);
}

.input-container .title-placeholder {
    position: absolute;
    bottom: 18px;
    transition: all 0.3s ease;
    margin-left: 12px;
}

.input-container input:focus+label .title-placeholder {
    transform: translateY(-100%);
    font-size: 14px;
    color: #6200ee;
}

.input-container input:valid+label .title-placeholder {
    transform: translateY(-100%);
    font-size: 14px;
}

.input-container .input__active:disabled+label .title-placeholder {
    transform: translateY(-100%);
    font-size: 14px;
}

.title-placeholder {
    font-size: 16px;
}
